<h1>props</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>属性名称</th>
<th>描述</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>双向绑定当前时间戳(毫秒)</td>
<td>Number</td>
<td>当天12点时间戳</td>
</tr>
<tr>
<td>level</td>
<td>当前可视时间轴区域所能表示的时间（小时）</td>
<td>Number</td>
<td>24</td>
</tr>
<tr>
<td>recordInfo</td>
<td>回放信息</td>
<td>Array</td>
<td>无</td>
</tr>
<tr>
<td>timeLineColor</td>
<td>定时录像条颜色</td>
<td>String</td>
<td>#1fa0fe</td>
</tr>
<tr>
<td>eventLineColor</td>
<td>事件录像条颜色</td>
<td>String</td>
<td>#fc6e30</td>
</tr>
<tr>
<td>lineColor</td>
<td>轴背景颜色</td>
<td>String</td>
<td>#ccc</td>
</tr>
</tbody>
</table>
<h1>events</h1>
<table class="table table-striped table-bordered"><thead>
<tr>
<th>事件名称</th>
<th>描述</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>activeLine</td>
<td>焦点轴</td>
<td>焦点轴的索引</td>
</tr>
</tbody>
</table>
<h1>说明</h1>
<p>这个时间轴只保留了最根本的轴体，不包括之前的标题、日历、图例，使用时可以根据v-model与level自行添加</p>
</section></template>

<script>
export default {
  data() {
    return {
      recordInfo: [
        {
          title: '通道1',
          eventVideo: [{ start: new Date().getTime(), end: new Date().getTime() + 10000000 },
          { start: new Date().getTime() + 10500000, end: new Date().getTime() + 11000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() }]
        },
        {
          title: '视频通道2',
          eventVideo: [{ start: new Date().getTime() - 5000000, end: new Date().getTime() + 10000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() }]
        },
        {
          title: '通道3',
          eventVideo: [{ start: new Date().getTime(), end: new Date().getTime() + 10000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() },
          { start: new Date().getTime() + 10005000, end: new Date().getTime() + 10010000 }]
        },
        {
          title: '通道4',
          eventVideo: [{ start: new Date().getTime(), end: new Date().getTime() + 10000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() - 1000000 }]
        },
        {
          title: '自定义通道5',
          eventVideo: [{ start: new Date().getTime(), end: new Date().getTime() + 10000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() }]
        },
        {
          title: '通道6',
          eventVideo: [{ start: new Date().getTime(), end: new Date().getTime() + 10000000 }],
          timedVideo: [{ start: new Date().getTime() - 10000000, end: new Date().getTime() }]
        }
      ],
      value: new Date().getTime(),
      levels: [1/60, 1/5, 1, 6, 12, 24], // 5s/60s, 60s/12m, 5m/1h, 0.5h/6h, 1h/12h, 2h/24h
      step: 5
    }
  },
  methods: {
    zoomOut() {
      if (this.step === 0) return
      this.step--
    },
    zoomIn() {
      if (this.step === this.levels.length - 1) return
      this.step++
    },
    activeLine(index) {
      console.log(index)
    },
    now() {
      const date = new Date(this.value)
      const addZero = (n) => (n.toString().length < 2 ? '0' + n : '' + n)
      return `${addZero(date.getFullYear())}-${addZero(date.getMonth() + 1)}-${addZero(date.getDate())} ${addZero(date.getHours())}:${addZero(date.getMinutes())}:${addZero(date.getSeconds())}`
    }
  }
}
</script>


